import { Animation } from "./animation.js"
import { Hotspots } from "./hotspots.js";
import { TilesController } from "./tilesController.js";
import { InputSystem } from "./input_system";
import { Resizer } from "./resizer";

/**
 * Animator handles drawing and animations interaction
 */

class Animator{

    /**
     * 
     * @param {Object} canvasGroup - all tools canvas 
     * @param {Array<String>} animationsPattern - array of animations path
     * @param {Number} countFrames - total frames per animations 
     * @param {Number} tlTimeDraw - timeline interval to draw a frame, in milisecond
     * @param {Object} options
     * @param {Array} hotspotRegions - hotspots regions , this is getting from the config file
     */
    constructor (canvasGroup , animationsPattern , countFrames , tlTimeDraw , options , hotspotRegions) {

        this.canvasGroup = canvasGroup;

        this.animationsPattern = animationsPattern;

        this._options = options,

        this.countFrames = countFrames;

        this.hotspots;

        
        this._tlTimeDraw = tlTimeDraw;

        this.maxWidth = this._options.maxWidth;
        this.maxHeight = this._options.maxHeight;

        this._tilesCanvas = canvasGroup.tilesCanvas;
        this._animCanvas = canvasGroup.animation;
        this._eventsCanvas = canvasGroup.eventsCanvas;

        this._currentWidth = 0;
        this._currentHeight = 0;

        this._hotspotRegions = hotspotRegions;
        
        this._animations = [];

        this._animationIndex = 0;
        this._frameIndex = 3;
        this._reverse = false;
        this._isPlaying = false;
        this._currentFrame;
        this._animationsReady = false;

        

        this._tilesController;
        this._resizer;

        this.percentLoaded = 0;
        this._countLoaded = 0;

        this.init();
        
    }

    init() {
        
        this._animCtx = this._animCanvas.getContext("2d");
        this._tilesCtx = this._tilesCanvas.getContext("2d");

        this._loadAnimations();
    }
    
    /**
     * Load preloaded animations images
     * @private
     */
    _loadAnimations() {

        console.log("loading..");

        let loadPromises = [];

        const total = this.animationsPattern.length * this.countFrames;
        for (let i = 0; i < this.animationsPattern.length; i++) {
            const promise = new Promise( (resolve , reject) => {
                const pattern = this.animationsPattern[i];
                const anim = new Animation(pattern , this.countFrames , 
                (error) => {
                    if (typeof error !== "undefined") {
                        reject(error);
                    }else{
                        
                        //this.percentLoaded = (loaded / total) * 100;
                        //console.log(this.percentLoaded);

                        this._animations.push(anim);
                        resolve();
                    }
                } , 
                () => {
                    this._countLoaded++;
                    this.percentLoaded = Math.round( (this._countLoaded / total) * 100 );
                    window.dispatchEvent(new CustomEvent("onImageAdded" , {
                        bubbles : false
                    }))
                });
            })

            loadPromises.push(promise);
        }

        Promise.all(loadPromises)
            .then( () => {
                console.log("all animations loaded!");
                this._setup();
                this._animationsReady = true;
            } )
            .catch( (e) => {
                alert("error while loading frames!");
                console.error(e);
                throw new Error('Error while loading animations! please check your images...');
            } )
    }

    /**
     * setup the animator to be ready.
     * Once ready we dispatch the "OnAnimatorReady" custom event
     * @private
     */
    _setup() {
        
        this._resizer = new Resizer(this.canvasGroup);
        this._initTiles();

        this.hotspots = new Hotspots(this.canvasGroup, this._hotspotRegions);

        this._inputSystem = new InputSystem(this._eventsCanvas , 
            {
                width : this.maxWidth , 
                height : this.maxHeight
            },
            this._options.zoomOptions,
            this._options.dragOptions,
        );
        

        this._inputSystem.rect.addEventListener('OnZoom' , () => {
            this._onZoomEvent();
        });

        this._inputSystem.rect.addEventListener('OnDrag' , (e) => {
            this.onDragEvent(e);
        });


        window.onresize = () => {
            this._resizer.resize();
            this._inputSystem.zoom.setMaxZoom();
            this.requestFrameDraw();
        };

        window.dispatchEvent(new CustomEvent("OnAnimatorReady", {
            bubbles: false
        }));
    }

    /**
     * instantiate tiles controller
     * @private
     */
    _initTiles() {
        this._tilesController = new TilesController(this._options);
    }

    /**
     * Run the Animator after the ready state
     */
    run() {
        this._showAnimationFrame(this._animationIndex , this._frameIndex);
    }

    play() {
        if(this._isPlaying)
            return;

        window.requestAnimationFrame( (timestamp) => {this._update(timestamp)} );
        this._isPlaying = true;
    }

    stop() {
        if(!this._isPlaying) return;
        window.cancelAnimationFrame( (timestamp) => {this._update(timestamp)} );
        this._isPlaying = false;
    }

    /**
     * Timeline Update loop
     * @param {Number} timestamp - timestamp after next draw
     * @private
     */
    _update(timestamp) {

        if(!this._animationsReady) {
           return;
        }
        
        if (this._startTime === undefined) {
            this._startTime = timestamp;
        }
        const elapsed = timestamp - this._startTime;
    
        if(elapsed >= this._tlTimeDraw && this._isPlaying) {
            this._step();
            this._startTime = timestamp;
        }

        window.requestAnimationFrame( (timestamp) => {this._update(timestamp)} );

    }

    /**
     * play one frame in the desired direction
     * @param {Boolean} reverse - if true we step the animation reversed
     * /@private
     */
    _step(reverse){
        reverse = (reverse == undefined) ? this._reverse : reverse;
        if(reverse) {
            this._setPrevIndex();
        }else{
            this._setNextIndex();
        }
        this._showAnimationFrame(this._animationIndex , this._frameIndex);
    }



    /**
     * draw call with request animation frame,
     * see if the browser is ready to draw 
     * @public
     */
     requestFrameDraw() {
        window.requestAnimationFrame( () => {this._draw()} );
    }

    /**
     * draw the current frame,
     * draw the hotspots,
     * load and draw the tiles.
     * @private
     */
    _draw(){

        //dont draw if animator is not ready 
        if(!this._animationsReady) {
            return;
        }

        /* const timestamp = Date.now();
        if (this.drawStartTime === undefined) {
            this.drawStartTime = timestamp;
        }
        const elapsed = timestamp - this.drawStartTime;
    
        if(elapsed <= 16.66) { //framerate simulation must refactor 
            return;
            
        }

        this.drawStartTime = timestamp; */

        
        // getting info about the draw size , frame , scaleFactor
        const anim = this._getAnimation();
        this._currentFrame = anim.getCurrentFrame(this._frameIndex);
        const scaleFactor = this._inputSystem.zoom.currentScale;

        // calculate the current width and height
        this._currentWidth = Math.round(this.maxWidth * scaleFactor);
        this._currentHeight = Math.round(this.maxHeight * scaleFactor);

        // calculate the positions of the draw
        const x = Math.round( (this._animCanvas.width*0.5) - (this.maxWidth*0.5) * scaleFactor );
        const y = Math.round( (this._animCanvas.height*0.5) - (this.maxHeight*0.5) * scaleFactor );

        // clearing the draw canvases , tiles must be cleared before the anim ctx(preloaded sizes canvas) 
        this._tilesCtx.clearRect(0, 0, this._tilesCanvas.width, this._tilesCanvas.height);
        this._animCtx.clearRect(0, 0, this._animCanvas.width, this._animCanvas.height);

        // draw the preloaded frame
        this._animCtx.drawImage(this._currentFrame , x, y , this._currentWidth , this._currentHeight);
        
        // draw hotspots
        this.hotspots.drawHts(this._animationIndex , this._frameIndex , this._currentWidth , this._currentHeight);
        
        /**
         * draw tiles if the canvas exist otherwise we do nothing.
         * Promise resolve canvas containing tiles or reject
         */
        const animIndex = this._animationIndex;
        const frameIndex = this._frameIndex;
        const pr = this._tilesController.fetchTiles(animIndex , frameIndex , this._currentWidth , scaleFactor);
        pr.then(canvas => {
            //check if scale is the same after loading the tile
            const scale = this._inputSystem.zoom.currentScale;
            if(scale !== scaleFactor || animIndex !== this._animationIndex || frameIndex !== this._frameIndex) {
                return;
            }
            this._tilesCtx.drawImage(canvas , x, y , this._currentWidth , this._currentHeight);
            canvas = null;
        }).catch(() => {
            return;
        });
    }

    /**
     * Switch to a desired Animation
     * @param {Number} index - the animation index
     */
    switchAnimation(index) {
        if(index < 0 || index >= this._animations.length) {
            return;
        }

        this._showAnimationFrame(index , this._frameIndex);
    }


    /**
     * 
     * @param {Number} animIndex - the animation index
     * @param {Number} frameIndex - the frame index
     * @private
     */
    _showAnimationFrame (animIndex , frameIndex) {
        this._animationIndex = animIndex;
        this._frameIndex = frameIndex;
        this.requestFrameDraw();
    }

    

    /**
     * Play one frame
     * @param {Boolean} reverse - is reversed direction
     * @public
     */
    playFrame(reverse) {
        this.stop();
        this._step(reverse);
    }


    /**
     * get the current animation
     * @returns {Animation} the current animation
     * @private
     */
    _getAnimation() {
        return this._animations[this._animationIndex];
    }

    _setNextIndex() {
        this._frameIndex++;
        if (this._frameIndex >= this.countFrames) {
            this._frameIndex = 0;
        }
    }

    _setPrevIndex() {
        this._frameIndex--;
        if (this._frameIndex < 0) {
            this._frameIndex = this.countFrames-1;
        }
    }

    /**
     * draw when zoom changed
     * @private
     */
    _onZoomEvent(){
        this.stop();
        this.requestFrameDraw();
    }

    /**
     * draw when drag controls changed
     * @private
     */
    onDragEvent(e){
        this.playFrame(e.detail);
    }

}


export { Animator };