<template>

    <div class="parent row position-relative p-0 m-0 border border-0">

        <LoaderComponent :show="showLoader" :percentLoaded="percentLoaded"></LoaderComponent>

        <div class="r-menu pt-3 position-absolute left-0 top-0">
            <ul>
                <li>
                    <button class="btn btn-primary" @click="setLevel(0)">Level 1</button>
                </li>
                <li>
                    <button class="btn btn-primary" @click="setLevel(1)">Level 2</button>
                </li>
                <li>
                    <button class="btn btn-primary" @click="setLevel(2)">Level 3</button>
                </li>
                <li>
                    <button class="btn btn-primary" @click="setLevel(3)">Level 4</button>
                </li>

                <li>
                    <button class="btn btn-primary" @click="play(true)">Play</button>
                </li>

                <li>
                    <button class="btn btn-primary" @click="play(false)">Stop</button>
                </li>
                <li>
                    <button class="btn btn-primary" @click="step(true)">Prev</button>
                </li>
                <li>
                    <button class="btn btn-primary" @click="step(false)">Next</button>
                </li>

                <li>
                    <button class="btn btn-primary" @click="showFilter">Filter</button>
                </li>

            </ul>
        </div>

        <div class="orbital-canvas" id="orbital">
            <PopoverComponent></PopoverComponent>
        </div>

        <FilterComponent :min="49" :max="66" :step="0.1" :gap="0" :showToast="showToast" @onCloseToast="closeFilter"
            @onSurfaceChanged="surfaceChanged">
        </FilterComponent>

    </div>

</template>



<script>

import { Orbital } from "@/canvas-modules/orbital";
import PopoverComponent from "./PopoverComponent.vue";
import FilterComponent from "./FilterComponent.vue";
import LoaderComponent from "./LoaderComponent.vue";


export default {
    name: 'CanvasWorld',

    components: {
        PopoverComponent,
        FilterComponent,
        LoaderComponent
    },

    props: {
        msg: String
    },

    data() {
        return {
            orbital: null,

            showToast: false,

            showLoader: true,

            percentLoaded: 0
        }
    },

    methods: {
        init() {
            this.orbital = new Orbital("orbital");
            this.orbital.init();

            window.addEventListener("onImageAdded", () => {
                this.percentLoaded = this.orbital.getPercentLoaded();
            });

            window.addEventListener("OnAppReady", () => {
                this.showLoader = false;
            });
        },

        setLevel(level) {
            this.orbital.setLevel(level);
        },

        play(play) {
            if (play) this.orbital.playOrbital();
            else this.orbital.stopOrbital();
        },

        step(reverse) {
            if (reverse)
                this.orbital.prev();
            else
                this.orbital.next()
        },

        showFilter() {
            this.showToast = true;
        },
        closeFilter() {
            this.showToast = false;
        },


        surfaceChanged(surface) {
            this.orbital.filterHSByPrice(surface);
        }
    },

    mounted() {
        this.init();
    }
}
</script>


